.mainArticle {
  max-width: 60ch;
  position: relative;
  grid-area: 2/2/3/3;
  display: flex;
  flex-direction: column;
  gap: calc(var(--font-S) / 3);
}
.mainArticle > p:not(:has(img)) {
  text-indent: 0.8em;
}
article {
  font-size: var(--font-S);
  text-align: justify;
}
.titleAndDate {
  display: grid;
  grid-template: auto / 1fr 5fr max-content;
  justify-content: space-between;
  margin: 10px 0;
  text-align: center;
}
.articleTitle {
  grid-area: 1/2/2/3;
  justify-self: stretch;
  font-size: calc(var(--font-S) + 0.2rem);
  line-height: calc(var(--font-S) * 2);
  color: #73383e;
  width: 100%;
  text-align: center;
}
.articleTitle {
  background-image: url("../../elements/multimedia/titleUnderline.png");
  background-repeat: no-repeat;
  background-size: auto cover;
  background-position: 0 100%;
}
.articleLi::after {
  top: 100%;
  content: url("../../elements/multimedia/break-pomiedzy-newsami.png");
  display: block;
  width: 100%;
  text-align: center;
  margin: 1.5rem 0;
}
.articleLi.page:not(:has(.dropingVisible)) {
  padding: 0;
  margin: 0;
  &::after {
    height: 0;
    content: "";
    margin: 0;
  }
  .articleHiddenButton {
    background-color: transparent;
  }
}
.titleAndDate > p {
  grid-area: 1/3/2/4;
  text-align: right;
  font-size: calc(var(--font-S) - 0.2rem);
  color: #73383e;
  white-space: pre-line;
  align-self: center;
}
.ornamentLetter {
  float: left;
  height: 3.25rem;
  margin-right: 0.3rem;
}
.articleIMG {
  display: block;
  max-width: 100%;
  font-style: oblique;
  text-align: center;
  margin: 1rem auto;
}
.newsArticleHiddenShowButton {
  width: 100%;
  height: 2rem;
  justify-content: end;
  & > img {
    padding-left: 3ch;
  }
}

.codeElement {
  width: 100%;
  padding: 0 10%;
}
.ImageAndDescryptionElement {
  min-height: 10rem;
  display: grid;
  grid-template: auto 1fr/20% 80%;
  .itemName {
    background: #0000000f;
    grid-area: 1/2;
    text-decoration: underline;
    text-align: center;
  }
  .itemDescryption {
    text-indent: 2em;
    grid-area: 2/2;
    padding-left: 3ch;
  }
  img {
    grid-area: 1/1/3/2;
    align-self: center;
  }
}
.page {
  position: relative;
  margin-bottom: var(--font-S);
  &::after {
    content: url("../../elements/multimedia/break-pomiedzy-newsami.png");
    display: block;
    width: 100%;
    text-align: center;
    margin: 1.5rem 0;
    /* position: absolute;
    content: url(http://localhost:3000/STApp/static/media/break-pomiedzy-newsami.a449745â€¦.png);
    display: block;
    top: calc(100% - var(--font-S));
    left: 0; */
    /* background: conic-gradient(from 0deg, #323348 0, #0000) 0/50px,
      linear-gradient(45deg, #323348, #324358); */
    /* width: 100%; */
    /* height: var(--font-S); */
  }
  .articleHiddenButton {
    background-color: #d3c18d99;
    width: 100%;
    text-align: left;
    padding: calc(var(--font-S));
    border-radius: 20px;
    color: #9c815e;
    display: grid;
    grid-template-columns: 2fr 1fr;
    font-size: calc(var(--font-S) + 0.5rem);
    z-index: 1;
    position: sticky;
    position: -webkit-sticky;
    top: -10px;
    transition: all 1s;
    * {
      justify-self: center;
    }
    &:hover {
      div {
        transform: rotate(180deg);
      }
    }
    div {
      height: 50px;
      width: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: #d3c18d99;
      border-radius: 50%;
      border: #73383e solid 3px;
    }
  }
}
.error {
  text-align: center;
  h2 {
    font-size: 3rem;
    color: #73383e;
  }
  p {
    font-size: 2rem;
  }
}
