.cursive {
  font-style: italic;
}
.bold {
  font-weight: bolder;
}
.boldcursive {
  font-style: italic;
  font-weight: bolder;
}
.small {
  font-size: calc(var(--font-S) * 0.7);
  .ornamentLetter {
    float: left;
    margin-right: 0.3rem;
  }
}
.articleLink {
  display: inline;
  font-weight: bold;
  font-style: italic;
  font-size: calc(var(--font-S) * 0.9);
}
